                      queTitle:"Mr. Brown was going away for a week. Before he left, he said to his son, if anyone asks for me, you can tell him that your father has been out for doing something, and will be back in a week, then be sure to ask him to sit down for a cup of tea. OK, Dad, said his son. But he was afraid his son couldnt remember this, he wrote these words down on a piece of paper and gave it to him. His son put it into his small pocket, took it out and looked at it every now and then.",
                      score:4,
                      answer:"",
                      analysis:"you can you up"
                    }
                  ]
                }
              ]
            }
          ]
        }
      ],
      music:{
        isPlay:true,
        currentTime:0,
        maxTime:0,
      }
    }
  },
  // ,
  directives:{
     drag(el){
       el.onmousedown = function(e){
        var oBottom = document.getElementById("bottom")
        var oTop = document.getElementById("top")
        var oLine = document.getElementById("line")
        var oBox = document.getElementById("box")
        var oTip = document.getElementById("tip")
        var disX = (e || event).clientX
        oLine.left = oLine.offsetLeft
         document.onmousemove = function(e){
            var iT = oLine.left + ((e || event).clientX - disX)
           var e=e||window.event,tarnameb=e.target||e.srcElement
            var maxT = oBox.clientWight - oLine.offsetWidth-8
            oLine.style.margin = 0
            iT < 15 && (iT = 15)
           // iT > maxT && (iT = maxT)
           if((iT+15)>=1000){
             iT = 1000-20
           }
            oLine.style.left = oTop.style.width = iT + "px"
            oTip.style.left = iT + "px"
            oBottom.style.width = oBox.clientWidth - iT-8 + "px"
            return false
         }
         document.onmouseup = function(e){
            document.onmousemove = null
            document.onmouseup = null
           oLine.releaseCapture && oLine.releaseCapture()
         }
         oLine.setCapture && oLine.setCapture()
       }
      }
  },
  methods: {
   // 
   showCard() {
     this.dialogCard = true
   },
   // 
   showHandParperDialog() {
     this.dialogHandParper = true
   },
   // 
   showExamTime() {
     this.examTimeShowAndHidden = !this.examTimeShowAndHidden
   },
   editorReady(editorInstance) {
      var global = this
      var ueditorId = editorInstance.ueditorId
      this.editor.set(ueditorId, editorInstance)
      // 
      editorInstance.setHeight(250)
      editorInstance.setContent('1241443')
      editorInstance.addListener('contentChange', () => {
        console.log(editorInstance.getContent())
        // 
        editorInstance.getKfContent(function(content) {
          setTimeout(function() { // 
          }, 500)
        })
      })
    },
    // 
    nextTest() {
      var find = 0 // 01nextId
      for (const item of this.questionsList) { 
        for (const ques of item.questions) {
          if (find===1) { // 
            this.upId = this.showId
            this.showId= ques.queId
            this.title =item.name +"( "+item.count+"  "+item.score+" )"
            if (this.showId === this.endId){ // 
              this.nextId = ""
            } else { 
              this.nextId ='0' // 
            }
            return
          }
          if (ques.queId === this.showId) {
            find = 1
          }
        }
      }
    },
    // 
    upTest() {
      var find = 0 // 01 2nextId
      var upItem // for
      var upQues // for
      for (const item of this.questionsList) { 
        var count = 0
        for (const ques of item.questions) {
          if (ques.queId === this.showId) {
            find = 1
          }
          count++
          if (find===1 && count===1) { // 
            this.nextId = this.showId
            this.showId= upQues.queId
            this.title =upItem.name +"( "+upItem.count+"  "+ upItem.score+" )"
            if (this.showId === this.startId){ // 
              this.upId = ""
            } else { 
              this.upId ='0' // 
            }
            return
          } else if (find===1) {
            this.nextId = this.showId
            this.showId= upQues.queId
            this.title =item.name +"( "+item.count+"  "+ item.score+" )"
            if (this.showId === this.startId){ // 
              this.upId = ""
            } else { 
              this.upId ='0' // 
            }
            return
          }
          upQues = ques
        }
        upItem = item
      }
    },
    // 
    anserCardClick(id, item){
      this.showId= id
      this.title =item.name +"( "+item.count+"  "+ item.score+" )"
      this.dialogCard = false
      if (this.showId === this.startId){ // 
        this.upId = ""
      } else { 
        this.upId ='0' // 
      }
      if (this.showId === this.endId){ // 
        this.nextId = ""
      } else { 
        this.nextId ='0' // 
      }
    },
    // 
    submitParper() {
       this.$router.push({ path: "/handParperSuccess" })
    },
    // 
    obtainProgress() {
      var count=0
      for (const item of this.questionsList) {
        if (item.type === 7 ) { // 
        } else if (item.type === 6) { //           
        } else {
          for (const ques of item.questions) {
            if (item.type === 1 && ques.answer!=='' ) { // 
              count++
            } else if(item.type === 2 && ques.answer.length>0) { // 
              count++
            } 
          }
        }   
      }
      this.answerProgress = count
    },
    timeDown () {
       const endTime = new Date('2019/6/20')
       const nowTime = new Date()
       let leftTime = parseInt((endTime.getTime()-nowTime.getTime())/1000)
       let d = parseInt(leftTime/(24*60*60))
       let h = this.formate(parseInt(leftTime/(60*60)%24))
       let m = this.formate(parseInt(leftTime/60%60))
       let s = this.formate(parseInt(leftTime%60))
       if(leftTime <= 0){
           this.flag = true
          // 
       }
       this.time = `${d}${h}${m}${s}`
    },
    // 0
    formate (time) {
      if(time>=10) {
         return time
      } else {
         return `0${time}`
      }
    },
    // 
    handleCommand(command) {
      if (command === "loginout") {
        delCookie("accessTokenExam")
        this.$router.push("/login")
      }
    },
    // t
    // listenMusic(){
    //   if(!this.$refs.music){
    //     return
    //   }
    //   if(this.$refs.music.readyState){
    //     this.music.maxTime = this.$refs.music.duration
    //   }
    //   this.music.isPlay=!this.$refs.music.paused
    //   this.music.currentTime = this.$refs.music.currentTime
    // },
    // play(){
    //   if(this.$refs.music.paused){
    //     this.$refs.music.play()
    //   }else{
    //     this.$refs.music.pause()
    //   }
    //   this.music.isPlay=!this.$refs.music.paused
    //   this.$nextTick(()=>{
    //     document.getElementById('play').blur()
    //   })
    // },
    // formatTime(time){
    //   let it = parseInt(time)
    //   let m = parseInt(it/60)
    //   let s = parseInt(it%60)
    //   return (m<10?"0":"")+parseInt(it/60)+":"+(s<10?"0":"")+parseInt(it%60)
    // }
  },
  components: { UEditor},
  created() {
    var height=document.documentElement.clientHeight
    this.height = height-60
    if (this.questionsList.length>0 ) {
      this.showId = this.questionsList[0].questions[0].queId
      this.startId = this.showId
      this.title = this.questionsList[0].name +"( "+this.questionsList[0].count+"  "+this.questionsList[0].score+" )"
      // endId
      if (this.questionsList.length>1) {
        var endContent = this.questionsList[this.questionsList.length-1]
        this.endId = endContent.questions[endContent.questions.length-1].queId
        if(this.endId){
          this.nextId='0' //  
        }
      } else if(this.questionsList.length>0) {
         this.endId = this.showId
         this.nextId = ''
      }
    }
  },
  mounted() {
    let time = setInterval(()=>{
       if(this.flag == true){
           clearInterval(time)
       }
       this.timeDown()
   },500)
   // 
   // this.$nextTick(()=>{
   //    setInterval(this.listenMusic,1000)
   // })
  },
   watch: {
    // 
    questionsList: {
      deep: true,
      handler: function(newValue, oldValue) {
        if(time === '') {
          time = new Date().getTime() // 
        }
        var oldTime = Date.parse(new Date()) // 
        // ,5
        this.obtainProgress()
        if (time+5000<oldTime) {
         // 
         time = oldTime
        } 
      }
    },
  }
};
</script>
